.ongoing_card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 0.3px solid rgb(222, 214, 214);
  cursor: pointer;
  padding: 1rem 2.5rem 1rem 1rem;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  margin-bottom: 0.7rem;
}
.ongoing_card:hover,
.ongoing_card_active {
  background: rgba(75, 26, 133, 0.1);
}
.ongoing_card_inner {
  display: flex;
  gap: 0.8rem;
  align-items: center;
}
.ongoing_card_inner > img {
  height: 2.5vw;
  width: 2.5vw;
  border-radius: 50%;
}
.ongoing_card_text > h6 {
  font-weight: 500;
  font-size: 1vw;
  line-height: 17px;
  margin-bottom: 0.3vw;
  color: #121212;
  text-align: left;
}
.ongoing_card_text > p {
  font-weight: 400;
  font-size: 0.878vw;
  line-height: 15px;
  text-align: left;
  color: #121212;
  margin-bottom: 0;
}
.menu_icon {
  width: 1.5rem;
  cursor: pointer;
  position: absolute;
  /* right: 3%; */
  right: 1%;
}
.ongoing_card_count_date {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
.ongoing_card_count_container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
}
.ongoing_card_count_container_inner {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 0.3rem;
}
.ongoing_card_count_container > h6,
.ongoing_card_count_container_inner > h6 {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 1vw;
  line-height: 17px;
  margin-bottom: 0;
  color: #121212;
  border: 0.5px solid #cacaca;
  min-width: 60px;
  min-height: 30px;
  border-radius: 6px;
}
.ongoing_card_count_container > p,
.ongoing_card_count_container_inner > p {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 10px;
  line-height: 15px;
  margin-bottom: 0;
  color: #121212;
}
.application_card {
  width: 100%;
  min-height: 164px;
  background: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 12px;
  padding: 1rem;
  margin-top: 0.8rem;
  cursor: pointer;
}
.application_card_title {
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
  align-items: center;
}
.application_card_title > img {
  width: 24px;
  height: 24px;
}

.application_card_title > h6 {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: rgba(18, 18, 18, 0.8);
  margin: 0;
}
.application_card_info {
  margin-top: 1rem;
}
.application_card_info > div {
  font-weight: 400;
  font-size: 13px;
  line-height: 20px;
  color: rgba(18, 18, 18, 0.5);
}

.application_card_info > h6 {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #002bbe;
  margin-top: 1rem;
}
.admission_confirm_class_card {
  background: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 12px;
  display: flex;
  gap: 1vw;
  align-items: center;
  padding: 0.9vw;
  margin-bottom: 0.5vw;
  cursor: pointer;
}
.admission_confirm_class_card:hover,
.admission_confirm_class_card_active,
.application_card:hover,
.application_card_active {
  background-color: rgba(75, 26, 133, 0.1);
}
.admission_confirm_class_card > img {
  height: 2rem;
  width: auto;
}
.admission_collect_docs {
  margin-top: 1rem;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  margin-top: 0;
}
.admission_collect_docs > h6 {
  font-weight: 500;
  font-size: 15px;
  line-height: 17px;
  color: #121212;
  margin: 0;
  margin-bottom: 0.2rem;
}
.admission_confirm_class_count {
  display: flex;
  gap: 2vw;
}
.admission_confirm_class_count > p {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 0;
  color: rgba(18, 18, 18, 0.8);
}
